@import "reset.css";

.root-wrap {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
}
.header-container {
  background-color: rgba(3, 37, 65, 1);
}

.header-wrap {
  height: 64px;
  width: 80%;
  margin: 0 auto;
  display: flex;
  align-items: center;
}
.header-left-wrap {
  display: flex;
}

.header-left-wrap ul {
  display: flex;
  margin-left: 16px;
}
.header-left-wrap ul li {
  margin-right: 14px;
  padding: 8px;
}
.header-nav-item {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}

.page-container {
  width: 80%;
  margin: 0 auto;
  padding: 32px 0;
  font-size: 1.6rem;
  font-weight: 1000;
}

.movies-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.movie-container {
  width: 250px;
  margin: 16px;
  background-color: #373b69;
  color: #fff;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
}
.movie-container img {
  width: 100%;
}
.movie-info {
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center;
}
.movie-info h4 {
  margin: 0;
}

.movie-info span {
  margin-left: 3px;
}
.movie {
  width: 50%;
  margin: 0 auto;
}
.movie input {
  width: 200px;
  height: 30px;
  margin-bottom: 5px;
}
.movie button {
  background-color: #000;
  border-radius: 5px;
  color: #fff;
  font-size: 1rem;
  padding: 5px;
  cursor: pointer;
}
.movielist {
  display: flex;
  align-items: center;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 5px;
  margin-top: 10px;
}
.movielist-title {
  flex-grow: 1;
  font-size: 1.1rem;
  font-weight: 800;
}
.movielist-year {
  font-size: 0.7rem;
  color: red;
}
